  margin: 0 4px 0 4px;
}
.wylib-logitem .right.inactive {
  max-width: 2em;
}
.wylib-logitem .button.close:hover {
  background: #ffcccc;
}
.wylib-logitem .button.lower:hover {
  background: #ccffcc;
}
.wylib-loglist .button.isnot.not {
  background: #ffdddd;
}
.wylib-loglist .button.isnot {
  background: #f0f0f0;
}
