___CSS_LOADER_EXPORT___.push([module.id, `.wylib-menu {
  min-height: 3em;
  max-height: 40vh;
  display: flex;
}
.wylib-menu .menu,
.wylib-menu .submenus {
  display: inline;
}
.wylib-menu input {
  width: 3em;
  text-align: right;
}
.wylib-menu > .menu {
  width: calc(100% - 6px);
  position: relative;
}
.wylib-menu .menu tr:hover {
  background: lightblue;
}
.wylib-menu .menu table {
  border-collapse: collapse;
  background: #f6f6f6;
  user-select: none;
}
.wylib-menu .menu td {
  white-space: nowrap;
  padding-left: 4px;
  padding-right: 4px;
  border: 1px solid #f2f2f2;
}
.wylib-menu .icon {
  display: block;
  fill: #2482a4;
  stroke: #222222;
}
