// Module
___CSS_LOADER_EXPORT___.push([module.id, `.wylib-loglist {
  padding: 2px;
  border: 2px solid #cc9900;
  border-radius: 8px;
  background: #f8f8f8;
  /*    width: 100%; */
}
.wylib-loglist .connector {
  position: absolute;
}
.wylib-loglist .subdivision {
  padding: 3px;
  margin: 0 0 0 15px;
  display: flex;
  /* border: 1px solid orange; */
}
.wylib-loglist .header {
  cursor: move;
  position: relative;
  background: #e0e0e0;
}
.wylib-loglist .connector {
  position: absolute;
  left: 8px;
  top: 18px;
  width: 16px;
  height: auto;
  fill: none;
  stroke: #999999;
  stroke-width: 2;
}
.wylib-loglist .spacer .lower {
  position: absolute;
  left: 6px;
  top: 20px;
  height: 12px;
}
.wylib-loglist .button {
  border-radius: 4px;
}
.wylib-loglist .button .icon {
  height: 12px;
  width: 12px;
}
.wylib-loglist .button:hover {
  background: #bbeebb;
}
.wylib-loglist .button.andor.and {
  background: #aaaaee;
}
.wylib-loglist .button.andor {
  background: #eeee88;
}
