___CSS_LOADER_EXPORT___.push([module.id, `.wylib-strdoc {
  border: 0;
  margin: 0;
  padding: 0;
}
.wylib-strdoc .header {
  opacity: 0.26;
  transition: opacity 350ms ease-in;
}
.wylib-strdoc .header:hover {
  opacity: 0.94;
}
.wylib-strdoc .header .menudock {
  border: 1px;
}
@media print {
.wylib-strdoc .header {
    display: none;
}
}
.wylib-strdoc .preview .title {
  font-size: 120%;
  text-align: center;
  width: 100%;
  padding-bottom: 0.5em;
}
.wylib-strdoc .edit .editline {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  position: relative;
  left: -1em;
}
.wylib-strdoc .input.title {
  flex-grow: 2;
}
.wylib-strdoc .input.name {
  left: -1em;
  flex-grow: 1;
}
.wylib-strdoc .sourceline {
  width: 100%;
  display: flex;
}
.wylib-strdoc .input.source {
  flex-grow: 1;
}
.wylib-strdoc .content .input {
  user-select: text;
  cursor: text;
}
.wylib-strdoc textarea {
  width: 100%;
}
