// Module
___CSS_LOADER_EXPORT___.push([module.id, `.wylib-svgraph {
  touch-action: none;
}
.wylib-svgraph .graph {
  height: 85%;
  width: 98%;
}
.wylib-svgraph .tools {
  position: absolute;
  right: 10px;
}
.wylib-svgraph button {
  width: 100%;
  padding: 4px;
  background: #bbddff;
  display: block;
  border: 1px solid black;
}
.wylib-svgraph .sliders input {
  display: block;
}
.wylib-svgraph .menu {
  position: absolute;
  right: 0.25em;
  top: 0.25em;
}
.wylib-svgraph .menu .icon {
  height: 1em;
  width: 1em;
}
.wylib-svgraph .toolbox {
  display: none;
  border: 1px solid blue;
  border-radius: 4px;
  padding: 4px;
  transition: all 500ms ease-in-out;
}
.menu:hover + .toolbox {
  display: block;
}
.wylib-svgraph .toolbox:hover {
  opacity: 0.85;
  background: white;
  display: block;
}
.wylib-svgraph .graph {
  position: absolute;
}
