___CSS_LOADER_EXPORT___.push([module.id, `.wylib-svg {
  height: auto;
  background: blue;
  touch-action: none;
}
.wylib-svg .tools {
  position: absolute;
  right: 10px;
}
.wylib-svg button {
  width: 100%;
  padding: 4px;
  background: #bbddff;
}
.wylib-svg .sliders input {
  display: block;
}
.wylib-svg .menu {
  position: absolute;
  right: 0.25em;
  top: 0.25em;
}
.wylib-svg .menu .icon {
  height: 1em;
  width: 1em;
}
.wylib-svg .toolbox {
  opacity: 0.2;
  display: none;
  border: 1px solid blue;
  border-radius: 4px;
  padding: 4px;
  transition: all 500ms ease-in-out;
}
.menu:hover + .toolbox {
  display: block;
  opacity: 1;
}
.wylib-svg .toolbox:hover {
  opacity: 1;
  display: block;
}
.wylib-svg .graph {
  position: absolute;
}
