BACE,eAAA","sourcesContent":["/*\n * IMPORTANT:\n * In order to preserve the uniform grid appearance, all cell styles need to have padding, margin and border sizes.\n * No built-in (selected, editable, highlight, flashing, invalid, loading, :focus) or user-specified CSS\n * classes should alter those!\n */\n\n.slickgrid-container {\n  overflow: hidden;\n  outline: 0;\n  position: relative;\n  box-sizing: content-box;\n\n  .slick-group-header-columns {\n    position: relative;\n    white-space: nowrap;\n    cursor: default;\n    overflow: hidden;\n  }\n\n  .slick-group-header {\n    width: 100%;\n    overflow: hidden;\n    border-left: 0px;\n  }\n\n  .slick-group-header-column.ui-state-default {\n    position: relative;\n    display: inline-block;\n    overflow: hidden;\n    -o-text-overflow: ellipsis;\n    text-overflow: ellipsis;\n    height: 16px;\n    line-height: 16px;\n    margin: 0;\n    padding: 4px;\n    border: 1px solid $grid-border-color;\n    order-left: 0px;\n    border-top: 0px;\n    border-bottom: 0px;\n    float: left;\n  }\n\n  .slick-viewport,\n  .slick-top-panel-scroller,\n  .slick-header,\n  .slick-headerrow,\n  .slick-footerrow {\n    position: relative;\n    width: 100%;\n    border: 1px solid $grid-border-color;\n    border-right-color: transparent;\n    border-bottom-color: transparent;\n    border-right-width: 0;\n    border-bottom-width: 0;\n    margin: 0;\n    outline: 0;\n  }\n  .slick-viewport {\n    overflow: auto;\n//    &::-webkit-scrollbar {\n//      -webkit-appearance: none;\n//    }\n//    &::-webkit-scrollbar-thumb {\n//      border-radius: 4px;\n//      border: 2px solid white; /* should match background, can't be transparent */\n//      background-color: rgba(0, 0, 0, .5);\n//    }\n  }\n  .slick-header,\n  .slick-headerrow,\n  .slick-footerrow {\n    overflow: hidden;\n  }\n  .slick-headerrow {\n    border-top-color: transparent;\n    border-top-width: 0;\n  }\n\n  .slick-top-panel,\n  .slick-header-columns,\n  .slick-headerrow-columns,\n  .slick-footerrow-columns {\n    position: relative;\n    white-space: nowrap;\n    cursor: default;\n    overflow: hidden;\n    margin: 0;\n    padding: 0;\n    border: 0;\n    outline: 0;\n  }\n\n  .slick-cell,\n  .slick-header-column,\n  .slick-headerrow-column,\n  .slick-footerrow-column {\n    position: absolute;\n    top: 0;\n    bottom: 0;\n\n    border: 1px solid $grid-border-color;\n    border-top-color: transparent;\n    border-left-color: transparent;\n    border-top-width: 0;\n    border-left-width: 0;\n    margin: 0;\n    padding: 0;\n\n//    overflow-x: scroll;\t\t//Obscures cell contents on Windows\n    overflow: hidden;\n//    text-overflow: ellipsis;\n    vertical-align: middle;\n//    z-index: 1;\n    white-space: nowrap;\n    cursor: default;\n\n    // do not include a bottom border for the bottom-most = leaf header cells:\n    // that border will be provided by the rows container.\n    &.slick-header-is-leaf {\n      border-bottom-color: transparent;\n      border-bottom-width: 0;\n    }\n  }\n\n  .slick-header-column.ui-state-default {\n    position: relative;\n    display: inline-block;\n    box-sizing: content-box !important;\n    overflow: hidden;\n//    text-overflow: ellipsis;\n    height: 1em;\n    line-height: 1em;\n    margin: 0;\n    padding: 4px;\n    border-right: 1px solid $grid-border-color;\n    border-left: 0px !important;\n    border-top: 0px !important;\n    border-bottom: 0px !important;\n    float: left;\n  }\n\n\n  .slick-cell {\n    box-sizing: border-box;\n    border-style: $grid-border-style;\n    padding: 1px 2px 1px 2px;\n  }\n\n  .slick-header-column {\n    padding: $cell-padding-top $cell-padding-right $cell-padding-bottom $cell-padding-left;\n  }\n\n  .grid-canvas {\n    position: relative;\n    outline: 0;\n  }\n\n  .slick-row {\n    position: absolute;\n    border: 0;\n    width: 100%;\n  }\n\n  .slick-header-column-sorted {\n    font-style: italic;\n  }\n\n  .slick-sort-indicator {\n    display: inline-block;\n    width: 8px;\n    height: 5px;\n    margins: 0;\n    position: absolute;\n    left: 2px;\n    bottom: 3px;\n  }\n  .slick-header-column-order {\n    display: inline-block;\n    position: absolute;\n    width: 10px;\n    height: 16px;\n    margins: 0;\n    left: 4px;\n    bottom: 6px;\n    padding: 0;\n//border: 1px solid red;\n    font-size: 0.75em;\n  }\n\n  .slick-sort-indicator-desc {\n//    background: url('../images/sort-desc.png');\n    border-left: 5px solid transparent;\n    border-right: 5px solid transparent;\n    border-top: 5px solid black;\n  }\n\n  .slick-sort-indicator-asc {\n//    background: url('../images/sort-asc.png');\n    border-left: 5px solid transparent;\n    border-right: 5px solid transparent;\n    border-bottom: 5px solid black;\n  }\n\n  .slick-header-sortable .slick-column-name {\n    margin-left: 10px;\n  }\n\n  .slick-header.ui-state-default {\n    box-shadow: 0 1px 2px rgba(0,0,0,.1);\n  }\n\n  .slick-column-name {\n//    position: absolute;\n//    text-overflow: ellipsis;\n  }\n\n  .slick-resizable-handle {\n    position: absolute;\n    font-size: 0.1px;\n    display: block;\n    cursor: col-resize;\n    width: 5px;\n    right: 0;\n    top: 0;\n    height: 100%;\n  }\n\n  .slick-resizable-handle-hover {\n    background-color: #ccc;\n  }\n\n  .slick-sortable-placeholder {\n    background: silver;\n  }\n\n  .slick-group {\n  }\n\n  .slick-group-toggle {\n    display: inline-block;\n  }\n\n  .slick-cell {\n    &.highlighted {\n      background: lightskyblue;\n      background: rgba(0, 0, 255, 0.2);\n      transition: all 0.5s;\n    }\n\n    &.flashing {\n      border: 1px solid red !important;\n    }\n\n    &.editable {\n//      z-index: 3;\n      overflow: visible;\n      background: white;\n      border-color: black;\n      border-style: solid;\n    }\n\n    &:focus {\n      outline: none;\n    }\n  }\n\n  .slick-reorder-proxy {\n    display: inline-block;\n    background: blue;\n    opacity: 0.15;\n    cursor: move;\n  }\n\n  .slick-reorder-guide {\n    display: inline-block;\n    height: 2px;\n    background: blue;\n    opacity: 0.7;\n  }\n\n  .slick-selection {\n//    z-index: 2;\n    position: absolute;\n    border: 2px dashed black;\n  }\n\n  .slick-pane {\n    position: absolute;\n    outline: 0;\n    overflow: hidden;\n    width: 100%;\n  }\n}\n\n.flatpickr-wrapper {\n//  z-index: 5;\n}\n\n.interact-placeholder {\n  background: red !important;\n  display: inline-block;\n  float:left;\n  transform: translate(0px, -100%);\n}\n\n.interact-drop-active {\n  box-shadow: inset 0 0 8px rgba(7, 67, 128, 0.5);\n}\n\n.interact-can-drop {\n  opacity: .9;\n}\n\n\n.scrollbar-fix {\n  &::-webkit-scrollbar {\n    -webkit-appearance: none;\n  }\n}\n","\n  $grid-border-color: #a0a0a0;\n  $grid-border-style: solid;\n  $grid-header-background: #d8d8d8;\n\n  $grid-cell-color: #f0f0fc;\n  $grid-cell-color-odd: #fafaff;\n  $grid-cell-selected-color: #e8e8ff;\n\n  $cell-padding-top: 4px;\n  $cell-padding-right: 4px;\n  $cell-padding-bottom: 4px;\n  $cell-padding-left: 4px;\n\n  @import './slick.grid.scss';\n  @import './slick.theme.scss';\n\n  .wylib-mlb {\n//    border: 1px solid green;\n    height: 100%;\n    max-width: 80vw;\n  }\n  .wylib-mlb .slickgrid-container {\n//    border: 1px solid red;\n//    width: auto;\n  }\n  .wylib-mlb .align-right {\n    text-align: right;\n  }\n  .wylib-mlb .align-center {\n    text-align: center;\n  }\n  .wylib-mlb .align-left {\n    text-align: left;\n  }\n","/*\n * IMPORTANT:\n * In order to preserve the uniform grid appearance, all cell styles need to have padding, margin and border sizes.\n * No built-in (selected, editable, highlight, flashing, invalid, loading, :focus) or user-specified CSS\n * classes should alter those!\n */\n\n.slickgrid-container {\n  .slick-header-columns,\n  .slick-header-column {\n    background: $grid-header-background;\n    // background-color: #EBECEE;\n  }\n\n  .slick-header-columns {\n    border-bottom: 1px solid $grid-border-color;\n    //background: linear-gradient(rgba(0,0,0,0) 60%, rgba(0,0,0,.1));\n  }\n\n  .slick-header-column {\n    border-right: 1px solid $grid-border-color;\n    border-bottom: 1px solid $grid-border-color;\n  }\n\n  .slick-header-column:hover {\n    background: darken($grid-header-background, 2%);\n  }\n\n  .slick-header-column-active {\n    background: darken($grid-header-background, 5%) !important;\n  }\n\n  .slick-headerrow {\n    background: $grid-header-background;\n  }\n\n  .slick-headerrow-column {\n    background: #fafafa;\n    border-bottom: 0;\n  }\n\n  .grid-canvas {\n    background: white;\n  }\n\n  .slick-row {\n    background: white;\n    border: 0;\n    line-height: 20px;\n\n    .slick-cell {\n      background: white;\n\n      padding-top: $cell-padding-top;\n      padding-bottom: $cell-padding-bottom;\n      padding-left: $cell-padding-left;\n      padding-right: $cell-padding-right;\n      box-sizing: border-box;\n\n      &.invalid {\n        border-color: red;\n        -moz-animation-duration: 0.2s;\n        -webkit-animation-duration: 0.2s;\n        -moz-animation-name: slickgrid-invalid-hilite;\n        -webkit-animation-name: slickgrid-invalid-hilite;\n      }\n\n      &.selected {\n        background-color: $grid-cell-selected-color;\n      }\n\n      &.active {\n        border-color: rgba(0, 0, 0, .3);\n        border-style: solid;\n        border-width: 1px;\n\n        // We compensate for the all-around border (now 1px at top and left too!) by reducing the padding\n        padding-top: 2px;\n        padding-left: 3px;\n\n        input.editor-text {\n          position: absolute;\n          width: 100%;\n          height: 100%;\n          border: 0;\n          margin: 0;\n          background: transparent;\n          padding: 2px 3px 2px 3px;\n          transform: translate(-3px, -2px);\n        }\n\n      }\n    }\n\n    &.odd .slick-cell {\n      background: $grid-cell-color-odd or darken($grid-cell-color, 2%);\n\n      // make sure we override the regular background color:\n      &.selected {\n        background-color: $grid-cell-selected-color;\n      }\n    }\n\n    &.active-row .slick-cell {\n      background-color: rgb(226, 255, 253);\n\n      // make sure we override the regular background color:\n      &.selected {\n        background-color: $grid-cell-selected-color;\n      }\n    }\n\n    &.active-row.odd .slick-cell {\n      background-color: rgb(226, 255, 253);\n\n      // make sure we override the regular background color:\n      &.selected {\n        background-color: green;\n      }\n    }\n\n    &.loading {\n      opacity: 0.5;\n    }\n  }\n\n  .slick-group {\n    border-bottom: 2px solid silver;\n  }\n\n  .slick-group-toggle {\n    width: 9px;\n    height: 9px;\n    margin-right: 5px;\n\n    &.expanded {\n//      background: url('../images/collapse.gif') no-repeat center center;\n    }\n\n    &.collapsed {\n//      background: url('../images/expand.gif') no-repeat center center;\n    }\n  }\n\n  .slick-group-totals {\n    color: gray;\n    background: white;\n  }\n\n  .slick-sortable-placeholder {\n    background: silver !important;\n  }\n\n  @-moz-keyframes slickgrid-invalid-hilite {\n    from {\n      box-shadow: 0 0 6px red;\n    }\n    to {\n      box-shadow: none;\n    }\n  }\n\n  @-webkit-keyframes slickgrid-invalid-hilite {\n    from {\n      box-shadow: 0 0 6px red;\n    }\n    to {\n      box-shadow: none;\n    }\n  }\n\n  // ----------------------------------------------\n  // Excel-like header\n  // ----------------------------------------------\n\n  .slick-header-menubutton {\n    background-position: center center;\n    background-repeat: no-repeat;\n    border-left: thin ridge silver;\n    cursor: pointer;\n    display: inline-block;\n    position: absolute;\n\n    // The next few items are already defined in the slick-headermenu.scss file and it should stay that way, *unless* you also replace the button image included there.\n    //bottom: 0;\n    //right: 0;\n    //top: 0;\n    //width: 14px;\n  }\n\n  .slick-header-menu {\n    background: none repeat scroll 0 0 white;\n    border: 1px solid #BFBDBD;\n    min-width: 175px;\n    padding: 4px;\n    z-index: 9;\t\t//was: 100000;\n    cursor: default;\n    display: inline-block;\n    margin: 0;\n    position: absolute;\n\n    button {\n      border: 1px solid #BFBDBD;\n      background-color: white;\n      width: 45px;\n      padding: 4px;\n      margin: 4px 4px 4px 0;\n    }\n\n    .filter {\n      border: 1px solid #BFBDBD;\n      font-size: 8pt;\n      height: 400px;\n      margin-top: 6px;\n      overflow: scroll;\n      padding: 4px;\n      white-space: nowrap;\n      width: 200px;\n    }\n\n    .textfilter {\n      & > label {\n        display: inline-block;\n        margin-left: 5px;\n        margin-right: 10px;\n      }\n\n      & > input[type=text] {\n        width: 70%;\n      }\n    }\n  }\n\n  label {\n    display: block;\n    margin-bottom: 5px;\n  }\n\n  .slick-header-menuitem {\n    border: 1px solid transparent;\n    padding: 2px 4px;\n    cursor: pointer;\n    list-style: none outside none;\n    margin: 0;\n  }\n\n  .slick-header-menuicon {\n    background-position: center center;\n    background-repeat: no-repeat;\n    display: inline-block;\n    height: 16px;\n    margin-right: 4px;\n    vertical-align: middle;\n    width: 16px;\n  }\n\n  .slick-header-menucontent {\n    display: inline-block;\n    vertical-align: middle;\n  }\n\n  .slick-header-menuitem:hover {\n    border-color: #BFBDBD;\n  }\n\n  .header-overlay,\n  .cell-overlay,\n  .selection-cell-overlay {\n    display: block;\n    position: absolute;\n    z-index: 4;\t\t//was: 999;\n  }\n\n  .slick-cell > .editor-select {\n    position: absolute;\n    left: 0;\n    right: 0;\n    width: auto;\n    top: 0;\n    bottom: 0;\n    max-width: 100%;\n    min-width: 0;\n    margin: 0;\n  }\n\n  .slick-range-decorator {\n    z-index: \t5;\t//was: 100;\n    pointer-events: none;\n    background: transparent;\n    border: none;\n    outline: black;\n  }\n}\n\ndiv.slick-large-editor-text {\n  z-index: 5;\t\t//was: 10000;\n  position: absolute;\n  background: $grid-cell-color;\n  padding: 5px;\n  border: 1px solid rgba(0, 0, 0, .5);\n  box-shadow: 1px 1px 2px 1px rgba(0, 0, 0, .3);\n  textarea {\n    backround: transparent;\n    width: 250px;\n    height: 80px;\n    border: 0;\n    outline: 0\n  }\n  div {\n    text-align: right;\n    button {\n      background-color: darken($grid-cell-color, 10%);\n      border: 1px solid $grid-border-color;\n      cursor: pointer;\n      justify-content: center;\n      padding-left: 0.75em;\n      padding-right: 0.75em;\n      text-align: center;\n      white-space: nowrap;\n    }\n  }\n}\n"],"sourceRoot":""}]);
