  border: 1px solid blue;
  border-radius: 4px;
  background: white;
  padding: 4px;
}
.wylib-connect .header {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
}
.wylib-connect input {
  width: 100%;
}
.wylib-connect .label {
  padding: 0.4em 0 0 0.1em;
}
.wylib-connect .sitelist {
  border: 1px solid #cccccc;
  border-radius: 4px;
  padding: 4px;
  font-family: Helvetica;
  font-size: 0.8em;
  min-height: 6em;
  max-height: 12em;
  max-width: 30em;
  min-width: 15em;
  overflow-y: scroll;
}
