___CSS_LOADER_EXPORT___.push([module.id, `.wylib-win {
  touch-action: none;
  position: fixed;
  border-style: solid;
}
.wylib-win.toplevel {
  z-index: 1;
  min-width: 120px;
  min-height: 14px;
}
.wylib-win > .header {
  position: relative;
  margin: 1px 1px 0 1px;
  display: flex;
  flex-flow: row nowrap;
}
.wylib-win > .menus,
.wylib-win > .operations {
  flex: 0 0 auto;
}
.wylib-win .header > .handle {
  flex: 1 1 auto;
  overflow: hidden;
  display: flex;
}
.wylib-win .header .handle .label {
  padding: 0 0 0 0.3em;
  position: relative;
  display: inline-block;
  align-self: flex-end;
  white-space: nowrap;
}
.wylib-win .headerbar {
  display: flex;
}
.wylib-win .childmenu {
  display: flex;
  align-items: flex-end;
}
.wylib-win .content {
  margin: 0px 2px 1px 2px;
  user-select: none;
  overflow-y: auto;
  z-index: 1;
}
.wylib-win .wylib-menu {
  z-index: 2;
}
